* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

@font-face {
  font-family: "Montserrat";
  src: url(./Assets/Fonts/Montserrat-Regular.ttf);
  font-weight: regular;
  font-style: normal;
}

@font-face {
  font-family: "MontserratT";
  src: url(./Assets/Fonts/Montserrat-SemiBold.ttf);
  font-weight: bold;
  font-style: normal;
}

body {
  font-family: "Montserrat";
  background-color: black;
  color: white;
}
